* reset css
*/
.vux-header-back {
  color: white !important;
}
.weui-tab__panel {
  padding-bottom: 0 !important;
}
.weui-cell_select .weui-cell__bd:after {
  margin-top: -7px !important;
  -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0) rotate(90deg) !important;
          transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0) rotate(90deg) !important;
}
.vux-toast .weui-toast {
  width: 2.4rem !important;
  min-height: 1.6rem !important;
}
html,
body {
  height: 100%;
  width: 100%;
